import React, { useState, useEffect } from "react";
import CharacterSheet from "./views/CharacterSheet";

import * as server from "./server";

import "./styles.css";

export default function App() {
  const isSiteUpdating = false;

  const [isLoading, setIsLoading] = useState(true);
  const [allChars, setAllChars] = useState(null);
  const [charName, setCharName] = useState("Levi");
  const [char, setChar] = useState(null);

  useEffect(() => {
    if (isLoading && !allChars) {
      const getAllChars = async () => {
        let allChars = await server.getCharacterData();
        setAllChars(allChars);
        setIsLoading(false);
      };
      getAllChars();
    }
    if (charName && allChars) {
      for (let ch of allChars) {
        if (ch.name === charName) {
          console.log("ch match: ");
          console.log(ch);
          setChar(ch);
        }
      }
    }
  }, [charName, allChars]);

  return (
    <div>
      {isSiteUpdating && <Alert />}

      {char && !isLoading ? (
        <CharacterSheet char={char} setChar={setChar} />
      ) : (
        <div>loading...</div>
      )}
    </div>
  );
}

const Alert = () => (
  <div
    style={{
      backgroundColor: "rgb(13, 30, 26)",
      color: "darkorange",
      borderBottom: "6px double orange",
      padding: "1rem"
    }}
  >
    <span>{`ALERT: I'm logged in and am developing this site `}</span>
    <span style={{ borderBottom: "3px double gray" }}>{`right now`}</span>.
    <span>{` Things may be broken. If so, refresh or try again later.`}</span>
  </div>
);
