import React, { useState, useEffect } from "react";

import GroupList from "../components/GroupList";
import GroupListItem from "../components/GroupListItem";

const getJSONFieldByName = (obj, name, field) => {
  let res;
  obj.map((entry) => {
    if (entry.name === name) {
      res = entry[field];
    }
  });
  return res;
};

const xpNeeded = (level) => level * 7 + level;

const CharacterSheet = (props) => {
  const [displayMode, setDisplayMode] = useState("compact");
  const char = props.char;

  // const setChar = props.setChar;
  // const toggleDisplayMode = () =>
  //   setDisplayMode(displayMode === "compact" ? "normal" : "compact");
  return (
    <div className="App">
      {/* <button
        onClick={(e) => toggleDisplayMode()}
        style={{
          padding: "0.4rem",
          position: "absolute",
          top: 10,
          right: 10,
          opacity: "75%"
        }}
      >
        {displayMode === "compact" ? "compact" : "normal"}
      </button> */}
      <div className={displayMode}>
        <div className="single-item-container">
          <GroupListItem subtitle="Name" label={char.name} oneLine={true} />
        </div>

        <div className="single-item-container">
          <GroupListItem subtitle="Race" label={char.race} oneLine={true} />
        </div>
        <div className="single-item-container">
          <GroupListItem
            subtitle="Playbook"
            label={char.playbook}
            source={`${char.playbook}`}
            webURL={char.webURL}
            oneLine={true}
          />
        </div>

        <div className="single-item-container">
          <GroupListItem
            subtitle="Alignment"
            label={char.alignment.name}
            oneLine={true}
          />
        </div>

        <div className="single-item-container">
          <GroupListItem subtitle="Level" label={char.level} oneLine={true} />
        </div>

        <div className="single-item-container">
          <GroupListItem
            subtitle="XP"
            label={`${char.xp} / ${xpNeeded(char.level + 1)}`}
            oneLine={true}
          />
        </div>

        <div className="single-item-container">
          <GroupListItem
            subtitle="HP"
            label={
              char.basehp +
              getJSONFieldByName(props.char.abilities, "Constitution", "score")
            }
            oneLine={true}
          />
        </div>
      </div>
      <br />
      <GroupList
        groupName="abilities"
        char={props.char}
        setChar={props.setChar}
      />

      <div style={{ padding: 0, margin: 0 }}>
        <div className="group-item-label" style={{ textAlign: "center" }}>
          <b style={{ borderBottom: "1px solid gray", padding: "0 2rem" }}>
            BONDS
          </b>
        </div>

        <div className="fwmp">
          {char.bonds.map((bond) => (
            <div key={bond.description} className="single-item-container">
              <GroupListItem
                subtitle={bond.description}
                label=""
                oneLine={true}
              />
            </div>
          ))}
        </div>
      </div>
      <br />

      <GroupList groupName="moves" char={props.char} setChar={props.setChar} />

      <GroupList groupName="spells" char={props.char} setChar={props.setChar} />

      <GroupList groupName="gear" char={props.char} setChar={props.setChar} />
    </div>
  );
};

export default CharacterSheet;
